import react, {Component} from 'react';

import Header from './header';
import {Image} from 'bloomer'




export default props=>{
  return( <div style={{backgroundImage: "url('/static/images/background.png')", backgroundSize: 'cover', width: '100%'}}>
  <Image isHidden='touch' style={{ backgroundSize: 'cover', width: '100%'}}src='/static/images/background.png'  />
  <Header>
    {props.children}
    </Header>
    </div>)
}


